@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Forum&family=Inter:wght@100;200;300;400;500;600;700;800;900&family=Montserrat:wght@300;600&family=Noto+Sans:ital,wght@1,100&family=Open+Sans:wght@300;500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@1,500&display=swap');

input {
    width: 1.3vw;
}

body {
    background-color: #A6E0AE; 
    display: flex;
    justify-content: center;
    flex-direction: column;
    font-family: 'Inter';
    margin: 0px;
    padding: 0px;
    padding-top: 0vw;
}

#root {
    align-items: center;
    width: 99vw !important;

}

#Vib  {
    display: flex;
flex-direction: row;
justify-content: center;
margin: 0vh 10vw;
background-color: #72e285;
width: 60vw;
transition: 500ms;
margin: 0px;
}

.Vibs {
    font-size: 1.1vw;
    font-family: 'Inter';
}

.vib-assort {
    position: relative;
}

.vib-assort.active, .vib-menu.active, .vib-contacts.active, .vib-paying.active {
    background-color: #34e743;
}

.VibAssort-cart {
    -webkit-box-shadow: 0px -4px 30px -14px rgba(0,0,0,0.64);
    -moz-box-shadow: 0px -4px 30px -14px rgba(0,0,0,0.64);
    box-shadow: 0px -4px 30px -14px rgba(0,0,0,0.64);
    border: 0px !important;
    position: absolute;
    left: 0vw;
    top: 2.7vw;
    background-color: #72e285 !important;
    z-index: 100;
    font-size: 0.9vw;
    padding: 0px !important;
}

.VibAssort-cart:hover {
    background-color: #72e285;
}

.VibAssort-cart p {
    margin: 0px;
    padding: 0.75vw 1vw;
    transition: 500ms ease;
}

.VibAssort-cart p:hover {
    transition: 500ms ease;
    background-color: #34e743;
}

#Vib div:hover {
    transition: 500ms;
    background-color: #34e743;
}

#Vib div {
padding: 0.7vw 1.5vw;
}

.Main {
    display: flex;
    flex-direction: row;
    width: 60vw;
    margin-top: 1.5vw;
}

.MainContent {
    margin-left: 1.5vw;    
}

.SideBar, .ContactBar {
    background-color: #72e285;
    padding: 1.2vw;
    width: 14.5vw;
}

.SideBar li, .ContactBar li {
    list-style-type: none;
    margin-bottom: 4vw;
}

.SideBar h4 {
    margin-bottom: 0px;
    padding-bottom: 0px;
}

.SideBar div:first-of-type p {
    margin-top: 0.6vw ;
}

.SideBar h4, .SideBar p {
    font-size: 1vw;
}

.SideBar li ul, .ContactBar li ul {
    transition: 500ms;
}

.SideBar li ul:hover, .ContactBar li ul:hover {
    transform: scale(1.1);
}

.cp:hover  {
    cursor: pointer;
}

.MainContent {
    width: 44vw;
    background-color: #72e285;
    padding: 0vw 0vw;
    height: 100%;
}

.MainContent p:first-of-type, .OtzivContent p:first-of-type, .DostavkaContent p:first-of-type, .oformlenie {
    padding: 1vw;
    font-size: 1.2vw;
    background-color: #34e743;
    margin: 0px 0px 2vw;
}

.MainContent div {
    display: flex;
    flex-direction: row;
}

.MainContent div div {
    transition: 500ms;
}


.DostavkaContent {
    width: 41.65vw;
    margin-left: 1.5vw;
    background-color: #72e285;
    padding: 0vw 0vw;
    height: 100%;
}

.DostavkaContent p {
    margin: 0px 0px 0.7vw 1vw;
    font-size: 1vw;
}

.DostavkaContent b {
    font-size: 1.15vw;
}

.Otziv {
    padding-top: 1.5vw;
}

.OtzivContent {
    width: 41.65vw;
    margin-left: 1.5vw;
    background-color: #72e285;
    padding: 0vw 0vw;
    height: 100%;
}


.MaineContent {
    width: 44vw;
    background-color: #72e285;
    padding: 0vw 0vw;
    margin: 0vw 0vw 0vw 1.5vw;
    height: 100%;
}

.MaineContent div p {
    margin: 0.5vw 0vw;
}

.otzivinput {
    outline: none;
    border: none;
    background-color: #72e285;
    border-bottom: 1px solid gray;

    font-size: 1vw;

}

.otzivinput:focus {
    outline: none;
    border: none;
    background-color: #72e285;
    border-bottom: 1px solid gray;

    font-size: 1vw;   
}

.submitbutton {
    padding: 0.5vw;
    margin: 1vw;

    border: 0px;
    border-radius: 0.4vw;

    font-size: 1.1vw;
    background: #34e743;

    transition: 500ms ease;
}

#submitbutton:hover {
    transform: scale(1.1);
}


.Items {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding-left: 0.1vw;
    font-size: 1vw;
}

.Item, .fullItem {
    display: flex;
    flex-direction: column !important;
    width: 31%;

    padding: 0px;
    border: 1px solid gray;
    margin: 1%;
    transition: 500ms ease;
}

.Item:hover {
    transform: scale(1.05);
}

.Item img {
    width: 100%
}

.Item h2 {
    font-weight: 500;
    padding: 0px;
    margin: 0.5vw;
    font-size: 1.1vw !important;
}

.Item p {
    background-color: #72e285 !important;
    padding: 0px 0px 0px 0.5vw !important;
    font-size: 0.9vw !important;
    margin-bottom: 0vw !important;
}

.Item button {
    font-size: 1vw;
    padding: 0.5vw;
    padding-right: 1.2vw;
    border: 0px;
    margin-top: 0.5vw;

    font-size: 1.1vw;
    background: #34e743;
}

header {
    display: flex;
    flex-direction: row;
    margin-bottom: 4vw;
    width: 100%;
    position: relative;
}

header .nvidguk, header .Koshik {
    display: flex;
    flex-direction: row;
    margin: 0.4vw;
    transition: 500ms;
}

header .nvidguk p, header .Koshik p {
    padding: 0px;
    margin: 0px;
    font-size: 1vw;
    font-weight: 400;
}

header h1 {
    font-family: "Playfair Display";
    margin-right: 36vw !important;
}

header .nvidguk:hover, header .Koshik:hover, header .Koshik.active {
    transform: scale(1.05);
}

.shopCart {

    position: absolute;
    top: 4vw;
    width: 22vw;
    right: 0px;
    background-color: white;
    z-index: 10;
}

.itemm {
    position: relative;
    display: flex;
    flex-direction: row;
    width: 90%;
    border: 1px solid gray;
    margin: 1vw;
    transition: 500ms;
}

.itemm img {
    height: 4.4vw;
}

.itemm div h2 {
    font-size: 1.2vw;
    font-weight: 600;
    margin: 0.5vw 0vw 0.2vw 0.5vw;
}

.itemm div p {
    margin-left: 0.5vw !important;
}

.itemm .trash {
    position: absolute;
    top: 1.5vw;
    right: 0.4vw;
    font-size: 1.5vw;
    transition: 500ms;
}

.itemm .trash:hover {
    transform: scale(1.1);
}

.empty h2 {
    font-size: 1.5vw;
    font-weight: 500;
    margin-left: 5vw;
}

.fullItem {
    position: fixed;
    left: 0;
    top: 0;
    width: 100% !important;
    height: 100%;
    margin: 0px !important;
    background: rgba(0, 0, 0, 0.8);
    z-index: 9999;
    overflow: auto;
}

.fullItem > div {
    padding: 1vw;
    width: 32%;
    position: relative;
    margin-left: 33%;
    background-color: white;
    border-radius: 9px;
    font-size: 1.2vw;
    margin-bottom: 5%;
    margin-top: 3.5%;
}

.fullItem img {
    width: 100%;
    border-radius: 9px;
}

.fullItem h2 {
    font-size: 2vw;
    margin: 0.5vw 0px 0.5vw 0px;
}

.fullItem p {
    margin: 0.5vw 1vw 0.5vw 0px;
}

.krest {
    font-size: 2vw;
    position: absolute;
    right: 4%;
    top: 3%;
    transition: 500ms ease;
}

.krest:hover {
    transform: scale(1.1);
}

.fullItemButton {
    position: absolute;
    left: 0px;
    bottom: 0px;
    font-size: 1vw;
    padding: 0.7vw;
    padding-right: 1.2vw;
    border: 0px;
    margin-top: 0.5vw;
    width: 100%;

    font-size: 1.3vw;
    background: #34e743;
    border-radius: 9px;
    transition: 500ms ease-out;
}

.fullItemButton:hover {
    transform: scale(1.01);
}

.fullItemPrice {
    margin-bottom: 10% !important;
}

.category {
    width: 20%;
    border-radius: 9px;
    border: gray 1px solid;
    font-size: 1.1vw;
    padding: 0.2vw;
    padding-left: 0.4vw;
    transition: 500ms ease-in;
}

.category:first-of-type {
    margin-left: 0.5vw;
}

.category:hover {
    transform: scale(1.04);
}

.prices {
    width: 100%;
    display: flex;
    flex-direction: row;
    font-size: 1vw;
}

.prices input {
    outline: none;
    border: none;
    border-bottom: 1px gray solid;
    background-color: #72e285;
    width: 8.5vw;
    margin: 0px 1vw;
    font-size: 1vw;
}

.prices button {
    padding: 0.5vw;
    margin: 0vw;

    border: 0px;
    border-radius: 0.4vw;

    font-size: 1.1vw;
    background: #34e743;

    transition: 500ms ease;
}

.podskazka {
    padding: 0.4vw;
    font-size: 1vw;
    position: absolute;
    width: 15vw;
    background-color: white;
    border: 1px gray solid;
    border-radius: 4px;
    transition: 800ms step-end;
    left: 100%;
    bottom: 0px;
    margin: 0vw 0vw 0vw 0.5vw;
}

.zakaz {
    padding-left: 3vw;
    position: sticky;
    left: 10px;
    top: 0;
    width: 100vw;
    height: 100vh;
    z-index: 100;
    background-color: #A6E0AE;
    font-size: 1vw;
    overflow-y: auto;
    overflow-x: hidden;
}

.forSections {
    display: flex;
    flex-direction: row;
}

.OrdersZakaz {
    display: flex;
    flex-direction: row;
    max-width: 35vw;
    overflow-x: auto;
}

.itemm1img {
    height: 4.9vw !important;
}

body::-webkit-scrollbar {
    width: 6px;               /* ширина всей полосы прокрутки */
  }
  
body::-webkit-scrollbar-track {
    background: #72e285;        /* цвет зоны отслеживания */
  }
  
body::-webkit-scrollbar-thumb {
    background-color: gray;    /* цвет бегунка */
    border-radius: 10px;       /* округлось бегунка */
  }

.section1 {
    width: 50vw;
    padding-left: 3vw;
}

.section1 h3 {
    font-size: 1.25vw;
    font-weight: 600;
}

.pokazZakaz {
    position: fixed;
    top: 3vw;
    left: 3vw;
    z-index: 100;
    padding: 1vw;
    border-radius: 9px;

    display: flex;
    flex-direction: row;
    background-color: white;
}

.category.active1 {
    border-color: black;
}

.isTkan {
    margin-left: 0.62vw;
    margin-top: 0.35vw;
    margin-bottom: 0.7vw;
    max-width: 81%;
    overflow-x: auto;
}

.category1 {
    width: 20%;
    border-radius: 9px;
    border: gray 1px solid;
    font-size: 1.1vw;
    padding: 0.2vw;
    margin: 0.1vw 0.27vw 0.1vw 0.27vw;
    padding-left: 0.4vw;
    transition: 500ms ease-in;
}

.category1:hover {
    transform: scale(1.04);
}